@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto+Slab:wght@300&family=Sansita+Swashed&family=Spartan:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&family=Sansita+Swashed&family=Spartan:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100&family=Sansita+Swashed&family=Spartan:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background-color: #6ac7a0;
  height: 60px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.logo {
  height: 110%;
  padding-right: 10px;
}

.menu-bars {
  margin-left: 1.5rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #1f5049;
  width: 220px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  overflow: auto;
  top: 0;
  left: -90%;
  z-index: 1;
  transition: 900ms;
}

.nav-menu.active {
  left: 0;
  transition: 400ms;
  overflow: auto;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 8px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #fff !important;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: rgb(243, 181, 179);
  color: #fff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #1f5049;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

span {
  margin-left: 8px;
}

.footer {
  min-height: 140px;
  background: #20201e;
  color: #fff;
  position: relative;
  clear: both;
  padding: 20px;
  bottom: 0;
  display: inline-block;
  width: 100%;
  text-align: center;
  line-height: 1.8;
  font-size: 14px;
}

.carousel-container {
  position: relative;
  width: 100%;
}

.caption {
  font-size: 50px;
  top: 50%;
  bottom: 50%;
 
}

.carousel-container {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  width: 100%;
}

.dropdown-search {
  justify-content: space-between;
}




.recipe.search.btn {
  width: 20px;
}
.ingredients-search {
  justify-content: center; 
  text-align: center;
  margin: 0 auto 0 auto;
  padding-top: 20px;
}
.recipe-results {
  position: relative;
  align-content: center;
  margin: 0 auto 0 auto;
  font-size: 14px; 
  color: rgb(3, 3, 3);
  line-height: 0.5rem;
  left: 50%;
  right: 50%;
}

.list-group-item {
  align-content: center;
  line-height: 0.5vw;
}

.recipe-title {
  font-family: 'Raleway', sans-serif;
}

.recipe-text {
  font-family: 'Roboto Slab', serif;
}
.jumbotron {
  background-image: url(/static/media/cooking.5820d719.jpg);
  width: 100%;
  height: 250px;
  background-size: cover;
  text-align: center;
  /* filter: brightness(95%); */
  
}

.jumbotron-header {
  font-family: 'Raleway', sans-serif;
  color: rgb(43, 40, 40);
  bottom: 0;
  align-self: flex-end;
}
.recipe-search-form {
  justify-content: center; 
  text-align: center;
  margin: 0 auto 0 auto;
}
.sign-in-form {
  border-radius: 0rem;
  min-height: 550px;
  justify-content: center;
  align-items: center;
  background-image: url(/static/media/signup.0961cc71.jpg);
  justify-content: center;
  font-family: 'Raleway', sans-serif;
  display: flex;
  
}

.sign-in-segment {
  justify-content: center;
  margin: 0 auto 0 auto;
  border-radius: 0rem;
  align-items: center;
}

.signup-wrap {
  justify-content: center;
}

.btn.btn-success {
  background-color: rgb(139, 199, 169);
  color: rgb(22, 22, 22);
  border-color: rgb(139, 199, 169);
  border-radius: 0rem;
  font-size: 15px;
  text-align: center;
  font-weight: 200;
  width: 100%;
  margin: auto;
  justify-self: center;
  margin-top: 20px;
  margin-bottom: 15px;
}

.btn.btn-success:hover {
  background-color: rgb(224, 221, 221);
  border-color: rgb(124, 120, 120);
  color: rgb(37, 37, 37);
  font-weight: 500;
}

.header {
  text-align: center;
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.home, 
.recipes, 
.saved {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navbar {
  background-color: rgb(24, 23, 21);
  color: #fff;
}
